	data() {
		return {
			shoppingNum:50
		};
	},
	onReady() {
		
	},
	onShow(){
		this.loadMore()
	},
	methods:{
		loadMore(){
			cart().then(res=>{
				let [err,{data}]=res;
				if(data.code==-1){
					console.log(data)
					uni.showToast({
						title:data.msg,
						icon:"none"
					})
					uni.navigateTo({
						url:"../login/login"
					})
				}
			})
		},
		setItem(e){
			console.log(e)
		},
		setNum(e){
			console.log(e)
		}
	}
}
